import React from 'react';
import {Link} from "react-router-dom"
import Spinner from '../../widgets/Spinner';
import 'regenerator-runtime/runtime';
import {goFetch, getToken, setToken} from "../../../api/misc";

/**
 * 投资汇总
 */
class OrderAgg extends React.Component {

  constructor(props) {
    super(props);
    ['handleChange', 'handleLogin'].forEach(m => this[m] = this[m].bind(this));
    this.state = {
      orders: null,
      summary: {},
      token: getToken() || '',
      auth: null,
    }
  }

  async componentDidMount() {
    this.load();
  }

  async load(login = false) {
    const res = await goFetch('/orders/agg', {
      headers: {'user-token': this.state.token}
    });
    if (res.status === 401) {// 鉴权失败
      setToken('');
      this.setState({
        auth: false,
        token: ''
      });
      return;
    }

    const json = await res.json();
    const summary = {
      company_sold: 0,
      company_hold: 0,
      cost: 0.00,
      profit: 0.00,//已卖完股票的收益
      trades: 0,
      market_value: 0.00,
      charge: 0.00,
    };
    json.forEach(v => {
      summary.trades += v.total;
      summary.charge += v.charge;
      v.remaining = v.buy_quantity - v.sell_quantity;
      if (!v.remaining) {// 卖完了
        v.clz = 'is-negative';
        summary.company_sold++;
        summary.profit += v.sell_fee + v.dividend - v.buy_fee - v.dividend_tax;// 卖出总收益
        v.market_value = 0;
      } else {
        v.market_value = v.remaining * v.current;
        summary.company_hold++;
        summary.cost += (v.buy_fee + v.dividend_tax - v.sell_fee - v.dividend);// 当前持仓总成本
        summary.market_value += v.market_value;// 当前持仓总市值
      }
      v.profit = (v.sell_fee + v.dividend - v.buy_fee - v.dividend_tax + v.market_value).toFixed(2);// 单公司盈利
    });
    json.sort((a, b) => {
      const cmp1 = a.remaining - b.remaining;
      const com2 = a.profit - b.profit;
      if (cmp1 > 0) {
        return -1;
      }
      if (cmp1 === 0) {
        if (a.remaining > 0) {// 两个股票数相同，且都不为0
          return 0;
        }
        return -com2; // 两个都为0，谁收益大谁排在前面
      }

      return 1;
    });
    this.setState({
      auth: true,
      orders: json,
      summary,
    });
    if (login) {
      setToken(this.state.token);
    }
  }

  handleChange(e) {
    this.setState({
      token: e.target.value
    })
  }

  handleLogin() {
    this.load(true);
  }

  buildContent() {
    const {orders, auth, token} = this.state;

    if (auth === null) {
      return <div>
        加载中...
        <Spinner/>
      </div>
    }
    if (auth === false) {
      return <div>您需要登录才能访问<br/>
        <div className="field has-addons">
          <div className="control">
            <input className="input" type="text" placeholder="登录码" value={token} onChange={this.handleChange}/>
          </div>
          <div className="control">
            <a className="button is-info" onClick={this.handleLogin}>
              Login
            </a>
          </div>
        </div>
      </div>;
    }

    return !orders ? '没有订购记录' : (
      <table className='table is-striped' style={{width: '100%'}}>
        {thead}
        <tbody>
        {
          orders.map((o, i) => {
            const buy_avg = o.buy_fee / o.buy_quantity;
            const clz = o.current > buy_avg ? 'is-positive' : 'is-negative';
            return <tr key={i}>
              <td>{i + 1}</td>
              <td><Link to={'/d/Order/' + o.stock_symbol} title='交易详情'>{o.stock_name}</Link></td>
              <td>{o.total}</td>
              <td className={o.clz}>{o.remaining}(-{o.sell_quantity})</td>
              <td>{o.buy_fee}</td>
              <td>{buy_avg.toFixed(2)}</td>
              <td>{o.sell_fee}</td>
              <td>{o.sell_fee ? (o.sell_fee / o.sell_quantity).toFixed(2) : '-'}</td>
              <td className={clz}>{o.current}</td>
              <td>{o.market_value.toFixed(2)}</td>
              <td className={o.profit >= 0 ? 'is-positive' : 'is-negative'}>{o.profit}</td>
            </tr>
          })
        }
        </tbody>
      </table>
    );
  }

  render() {
    const {summary} = this.state;
    return <div className='container stock'>
      <article className="message is-dark">
        <div className="message-header">
          汇总
        </div>
        <div className="message-body">
          <div className="columns">
            <div className="column">
              <dl>
                <li>持有公司数: {summary.company_hold}</li>
                <li>卖出公司数: {summary.company_sold}</li>
                <li>交易总次数: {summary.trades}</li>
                <li>交易总费用: {summary.charge ? summary.charge.toFixed(2) : null}</li>
              </dl>
            </div>
            <div className="column">
              <dl>
                <li>持仓成本: {summary.cost ? summary.cost.toFixed(2) : null}</li>
                <li>当前市值: {summary.market_value ? summary.market_value.toFixed(2) : null}</li>
                <li>卖出收益: {summary.profit ? summary.profit.toFixed(2) : null}</li>
                <li>总收益: {summary.cost ? (summary.profit + summary.market_value - summary.cost).toFixed(2) : null}</li>
              </dl>
            </div>
          </div>
        </div>
      </article>
      {this.buildContent()}
    </div>
  }
}

const thead = (<thead>
<tr>
  <th>序号</th>
  <th>股票名称</th>
  <th>交易</th>
  <th>股票数</th>
  <th>买入</th>
  <th>均价</th>
  <th>卖出</th>
  <th>均价</th>
  <th>最新</th>
  <th>市值</th>
  <th>收益</th>
</tr>
</thead>);

export default OrderAgg
